<template>
  <div class="discover">
      <header>
          <div @click="goback()"><i class="fas fa-chevron-left"></i></div>
          <h3>发现</h3>
      </header>
      <div class="banner">
          <div class="login-show">
          </div>
      </div>
      <section class="item1">
          <div class="item1-t-l">
              <div class="item1-t-l-wrap">
                  <p>百元红包</p>
                  <p>兴业银行联名卡</p>
              </div>
              <img src="@/assets/elm/img/discover/item1-1.png" alt="">
          </div>
          <div class="item1-t-r">
              <div class="item1-t-r-wrap">
                  <div class="item1-item">
                      <p>金币商城</p>
                      <p>0元购物在这里</p>
                  </div>
                  <img src="@/assets/elm/img/discover/item1-2.png" alt="">
              </div>
              <div class="item1-t-r-wrap">
                  <div class="item1-item">
                      <p>推荐有奖</p>
                      <p>5元现金拿不拿</p>
                  </div>
                  <img src="@/assets/elm/img/discover/item1-3.png" alt="">
              </div>
          </div>
          <div class="item1-b-l">
              <div class="item1-t-r-wrap">
                  <div class="item1-item">
                      <p>周边优惠</p>
                      <p>领取口碑好券</p>
                  </div>
                  <img src="@/assets/elm/img/discover/item1-4.png" alt="">
              </div>
          </div>
          <div class="item1-b-r">
              <div class="item1-t-r-wrap">
                  <div class="item1-item">
                      <p>必吃爆料</p>
                      <p>最夯外卖指南</p>
                  </div>
                  <img src="@/assets/elm/img/discover/item1-5.png" alt="">
              </div>
          </div>
          <div class="entry">
              <router-link to="#">
                  <img src="@/assets/elm/img/discover/item1-6.png" alt="">
              </router-link>
          </div>
      </section>
      <div class="shoplist">
        <div class="shoplist-title">
          限时好礼
        </div>
        <div class="main">
            <router-link to="" v-for="item in discoverlist1" :key="item.id">
                <img :src="item.pic" alt="">
                <h3 class="main-title">{{item.title}}</h3>
                <p class="main-price"><span>¥</span>{{item.price}}</p>
            </router-link>
        </div>
        <p class="more">查看更多></p>
      </div>
      <div class="shoplist">
        <div class="shoplist-title">
          每日精选
        </div>
        <div class="main">
            <router-link to="" v-for="item in discoverlist2" :key="item.id">
                <img :src="item.pic" alt="">
                <h3 class="main-title">{{item.title}}</h3>
                <p class="main-price"><span>¥</span>{{item.price}}</p>
            </router-link>
        </div>
        <p class="more">查看更多></p>
      </div>
      <index-footer></index-footer>
  </div>
</template>
<script>
import api from '@/axios/api.js'
import '@/mock.js'
const indexFooter = () => import('@/components/footer/index-footer')
export default {
  components: {
      indexFooter
  },
  data: function () {
    return {
      indexlist:[],
      discoverlist1:[],
      discoverlist2:[],
    }
  },
  created(){
    api.getlist('/discoverlist').then(res => {
        this.discoverlist1 = res.list.slice(0,3);
        this.discoverlist2 = res.list.slice(3);
        console.log(res.list)
    })
  },
  mounted() {
  },
  methods: {
    goback: function () {
      window.history.length > 1
        ? this.$router.go(-1)
        : this.$router.push('/')
    }
  }
}
</script>

<style scoped>
.discover{
    background-color: #ededed;
    width: 100vw;
    padding-bottom: 30vw;
}
header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 15vw;
    font-size: 4vw;
    line-height: 15vw;
    text-align: center;
    color: white;
    z-index: 2;
}
header div{
    position: absolute;
    top:0;
    left: 0;
    width: 10vw;
    height: 15vw;
    background-color:transparent;
    z-index: 2;
}
header h3{
    width: 100vw;
    height: 15vw;
    background-color: rgb(35, 149, 255);
}
.banner{
    position: relative;
    width: 100vw;
    height: 56vw;
    margin-top: 15vw;
    background-repeat:no-repeat;
    background-size:100vw;
    background-image: url(../assets/elm/img/discover/title.png)
}

.item1{
    margin-top: .6vw;
    width: 100vw;
    height: auto;
    position: relative;
    background-color: white;
}
.item1-t-l{
    position: relative;
    width: 55vw;
    height: 40vw;
    padding: 4vw 2vw 2vw 3vw;
    background-color: white;
    border-bottom: 1px solid #ededed;
    border-right: 1px solid #ededed;
}
.item1-t-l-wrap{
    width: 60%;
    height: 100%;
}
.item1-t-l-wrap p{
    font-size: 4.8vw;
    color: rgb(232, 143, 40);
}
.item1-t-l-wrap p:last-child{
    font-size: 3.6vw;
    color: #999;
}
.item1-t-l img{
    position: absolute;
    bottom: 2vw;
    right: 2vw;
    width: 40%;
}
.item1-t-r{
    width: 45vw;
    height: 40vw;
    position: absolute;
    right: 0;
    top: 0;
}
.item1-t-r-wrap{
    width: 100%;
    height: 20vw;
    position: relative;
    padding:4vw 0 0 4vw;
    border-bottom: 1px solid #ededed;
}
.item1-t-r-wrap p{
    font-size: 4.8vw;
    color: rgb(232, 143, 40);
}
.item1-t-r-wrap p:last-child{
    font-size: 3.6vw;
    color: #999;
}
.item1-t-r-wrap img{
    width: 14vw;
    position: absolute;
    right: 2vw;
    top: 3vw;
}
.item1-t-r-wrap:last-child p:first-child{
    font-size: 4.8vw;
    color: rgb(27, 169, 225);
}
.item1-b-l{
    width: 55vw;
    height: 20vw;
    border-right: 1px solid #ededed;
}
.item1-b-r{
    width: 45vw;
    height: 20vw;
    position: absolute;
    right: 0;
    top: 40vw;
}
.entry{
    width: 100%;
}

.entry img{
    width: 100%;
}

.main{
    width: 100vw;
    height: auto;
    padding: 2vw 3vw;
}
.main a{
    display: inline-block;
    width: 30vw;
    height: auto;
    margin-right: 2vw;
}
.main a:last-child{
    width: 30vw;
    height: auto;
    margin-right: 0;
}
.main a img{
    width: 30vw;
    height: 30vw;
    margin-bottom: 4vw;
}
.main-title{
    color: #333;
    overflow: hidden;
    font-size: 3.6vw;
    margin-bottom: 2vw;
}
.main-price{
    color: #ff5339;
    overflow: hidden;
    font-size: 4vw;
}

.more{
    width: 100vw;
    height: 8vw;
    padding-bottom: 4vw;
    font-size: 3.6vw;
    line-height: 4vw;
    text-align: center;
    color: #999;
}



.shoplist{
    background-color: white;
    margin-bottom: 2vw;
}
.shoplist-title{
  width: 100vw;
  height: 10vw;
  background-color: white;
  display: flex;
  -moz-box-align: center;
  -webkit-box-align: center;
  align-items: center;
  justify-content:center;
  font-size: 4vw;
}
.shoplist-title::before{
  display: block;
  content: '';
  margin-right: 3.5vw;
  width: 5vw;
  height: .25vw;
  background-color:rgb(214, 212, 212);
}
.shoplist-title::after{
  display: block;
  content: '';
  margin-left: 3.5vw;
  width: 5vw;
  height: .25vw;
  background-color:rgb(214, 212, 212);
}
</style>



